import React from 'react'
import DarkIcon from '@material-ui/icons/Brightness4'
import LightIcon from '@material-ui/icons/Brightness7'

import { useSwitchTheme } from '../effects/useSwitchTheme'

export const ThemeSwitcher = ({ onChange }: { onChange?: () => boolean }) => {
  const [info, switchTheme] = useSwitchTheme()
  const handleSwitch = () => {
    if (onChange == null || onChange() == null) switchTheme()
  }
  return info.isLightTheme ? (
    <DarkIcon onClick={handleSwitch} />
  ) : (
    <LightIcon onClick={handleSwitch} />
  )
  // return <Switch checked={info.isLightTheme} onChange={} />
}
